.event-card {
  background: #111;
  overflow: hidden;
  margin: 1em;
  border-radius: 1em;
  min-width: 30em;
  max-width: 50em;
  width: 100%;

  &:hover {
    .card .txt {
      background-position: 0%;
    }
  }

  * {
    transition: all 0.2s;
  }
  a {
    color: inherit;
  }

  .card {
    display: flex;
    position: relative;
    min-height: 18em;

    .img {
      position: absolute;
      right: 0;
      // width: 15em;
      height: 100%;
      overflow: hidden;
      z-index: 2;
      img {
        // width: 100%;
        height: 100%;
      }
    }

    .txt {
      width: 100%;
      height: 100%;
      padding: 2em;
      position: absolute;
      background: red;
      font-family: "Product Sans";
      z-index: 3;
      background: linear-gradient(to right, #000, #000, #000, #0000);
      background-size: 110%;
      background-position: 100%;
      transition: background-position 0.3s;
      // backdrop-filter: blur(1px);
      .title {
        font-size: 2em;
        margin-bottom: 0.5em;
      }
      .desc {
        height: 6em;
        display: flex;
        padding: 0 0.5rem;
        width: 80%;
        overflow: hidden;
        color: #bbb;
        // font-size: 1.1em;

        // font-family: monospace;
      }
      .date {
        display: flex;
        align-items: center;
        > * {
          border-radius: 0.3em;
        }
        // time {
        //   display: flex;
        //   flex-wrap: wrap;
        // }
        .date {
          margin-right: 0.5em;
        }
        .time {
          color: #aaa;
          padding: 0;
        }
        .start {
          color: #a6ffcb;
          // background: #a6ffcb22;
          padding: 0.3em 0.5em;
        }
        .to {
          display: inline-block;
          padding: 0 0.5em;
        }
        .end {
          color: #ed4264;
          // background: #ed426422;
          padding: 0.3em 0.5em;
        }
      }
      .venue {
        color: #1fa2ff;
        margin-top: 0.5em;
        padding: 0.5rem;
      }
      .details {
        color: #fff;
        font-size: 1.5em;
        padding: 0.3rem;
        width: 100%;
        text-align: center;
        font-family: monospace;
        opacity: 0.3;
        &:hover {
          opacity: 0.8;
          // backdrop-filter: blur(5px);
        }
      }
    }
  }
}
